.faqPage {
  position: relative;
  padding: 24px;
}

@media (min-width: 992px) {
  .faqPage {
    padding: 40px 384px 40px 40px;
  }
}

.title {
  margin-bottom: 24px;
  font-size: 24px;
  text-transform: uppercase;
}

.aside {
  display: none;
}

@media (min-width: 992px) {
  .aside {
    position: fixed;
    top: 104px;
    right: 40px;
    display: block;
    width: 304px;
    background-color: white;
    border-radius: 4px;
    box-shadow: 0px 3px 6px #00000029;
    overflow: hidden;
  }
}

[dark-theme] {
  .aside {
    background-color: #242b4384;
    box-shadow: none;
  }
}

.links {
  padding: 24px;
  max-height: calc(100vh - 64px - 80px);
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 8px;
  }
  &::-webkit-scrollbar-track {
    background-color: rgba(0, 0, 0, 0.025);
  }
  &::-webkit-scrollbar-thumb {
    border: none;
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 0;
  }
}

.link {
  font-size: 12px;
  color: #8e8e63;
  line-height: 1.4;
  cursor: pointer;
  &:hover {
    text-decoration: underline;
  }
  &:not(:first-child) {
    margin-top: 16px;
  }
}

.block {
  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

.blockTitle {
  margin-bottom: 16px;
  font-size: 20px;
}

.paragraph {
  margin-bottom: 24px;
  line-height: 1.4;
  &:last-child {
    margin-bottom: 0;
  }
}

.subtitle {
  font-size: 14px;
  font-weight: 500;
  opacity: 0.8;
}

.text {
  font-size: 14px;
  white-space: pre-line;
  opacity: 0.6;
  a {
    color: rgb(0, 153, 188);
  }
}
